#intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.bg{
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
}
.introContent{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 2.2rem;
    font-weight: 550;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
    font-size: 1rem;
    font-weight: 300;
}
.introName{
    color: #37FF8B;
}
.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}
.btn{
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}
.btnImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 0 0.25rem;
}

@media screen and (max-width: 840px){
    .bg{
        right: -10vw;
    }
    .introContent{
        font-size: 6vw;
    }
    .hello{
        font-size: 3vw;
    }
}

@media screen and (max-width: 480px){
    .bg{
        right: -20vw;
    }
}